/* contactForm.css */

.contact-form-container {
    background-color: #f8f9fa;
    padding: 50px 20px;
  }
  
  .form-column {
    background-color: #fff;
    padding: 30px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border: 4px solid black;
  }
  
  .info-column {
    background: rgb(250,4,4);
    background: linear-gradient(90deg, rgba(250,4,4,1) 0%, rgba(0,0,0,1) 91%);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 4px solid black;
  }
  
  .form-input {
    border-radius: 4px;
    margin-bottom: 20px;
    /* Add other input styles as needed */
  }
  
  .submit-button {
    width: 100%;
    margin-top: 20px;
    /* Add other button styles as needed */
  }
  
  .contact-info {
    text-align: center;
  }
  